
import { PropType, defineComponent } from 'vue';
import { FormValidation } from '@/shared/services/FormValidationService';
import { IbUserDetail } from '@/shared/type/CIB20100311.type';
import { modalController } from '@ionic/vue';

const formValidation = new FormValidation()

export default defineComponent({
  name: "cib201003ss11",

  props: {
    data: {
      type: Object as PropType<IbUserDetail>,
      required: true
    }
  },

  data() {
    return {
        userID: "",
        customerNo: "",
        email: "",
      isEditable: false,
    }
  },

  watch: {
    data(customerData: IbUserDetail) {
      this.userID = customerData.userInfo.userID;
      this.customerNo = customerData.userInfo.customerNo;
      this.email = customerData.userInfo.email;
    },

    isEditable(isAllowTabChange) {
      this.$emit('isBeingEdited', isAllowTabChange)
    }
  },

  methods: {
    onClickOperation(btn: string) {
      switch (btn) {
        case "edit":
          this.isEditable = true;
          break;

        case "cancel":
              this.isEditable = false;
          break;

        case "list":
          this.$router.push('/corporate-internet-banking/subscription');
          break;

        case "submit":
          if(!formValidation.validateEmail(this.email)){ // if email wrong format
            this.$dialog.onOpen({
              content: `${this.$t("message.invalid_email")}`,
              dialogMode: "Error",
              callback: ()=>{
                modalController.dismiss();
              }
            })
          }else{
            this.$dialog.onOpen({
              content: `${this.$t("message.would_you_like_to_save")}`,
              dialogMode:"Question",
              callback: ()=>{
                this.handleSubmit();
                modalController.dismiss();
              }
            });
          }
          break;
      }
    },

    async handleSubmit() {
      // const reqBody: CIB2010033A_REQ_BODY = {
      //   userID: this.userID,
      //   customerNo: this.customerNo,
      //   email: this.email
      // };

      // const responseData = await requestService.getRequest('CIB2010033A', reqBody, false) as CIB2010033A_RES;

      // if (responseData.header.result) {
      //   this.isEditable = false;
      //   this.$emit("isReseTButton", false);
      //   this.$dialog.onOpen({
      //     content: this.$t("message.update_success"),
      //     dialogMode:"Success",
      //       callback:()=>{
      //         modalController.dismiss()
      //       }
      //   });
      // }
    },
  }
})
